export default function UserDirectoryPage() {
  const [users, setUsers] = useState<User[]>(mockUsers);
  const [filteredUsers, setFilteredUsers] = useState<User[]>(mockUsers);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('All');
  const [designationFilter, setDesignationFilter] = useState<string>('All');
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' as 'success' | 'error' | 'warning' | 'info' });

  // Filter users based on search term and filters
  useEffect(() => {
    let filtered = users;

    if (searchTerm) {
      filtered = filtered.filter(
        (user) =>
          user.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
          user.employeeId.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (statusFilter !== 'All') {
      filtered = filtered.filter((user) => user.status === statusFilter);
    }

    if (designationFilter !== 'All') {
      filtered = filtered.filter((user) => user.designation === designationFilter);
    }

    setFilteredUsers(filtered);
  }, [users, searchTerm, statusFilter, designationFilter]);

  const handleCreateUser = () => {
    // This will be implemented with the drawer component
    setSnackbar({ open: true, message: 'Create user functionality will be implemented!', severity: 'info' });
  };

  const handleEditUser = (user: User) => {
    // This will be implemented with the drawer component
    setSnackbar({ open: true, message: `Edit user: ${user.name}`, severity: 'info' });
  };

  const handleViewUser = (user: User) => {
    // This will be implemented with the drawer component
    setSnackbar({ open: true, message: `View user: ${user.name}`, severity: 'info' });
  };

  const handleBulkUpload = () => {
    // This will be implemented with the modal component
    setSnackbar({ open: true, message: 'Bulk upload functionality will be implemented!', severity: 'info' });
  };

  const getUniqueValues = (field: keyof User): string[] => {
    return Array.from(new Set(users.map((user) => user[field] as string)));
  };

  const columns: GridColDef[] = [
    {
      field: 'employeeId',
      headerName: 'Employee ID',
      width: 120,
      fontWeight: 'bold',
    },
    {
      field: 'name',
      headerName: 'Name',
      width: 180,
      fontWeight: 'bold',
    },
    {
      field: 'designation',
      headerName: 'Designation',
      width: 160,
    },
    {
      field: 'ulb',
      headerName: 'ULB',
      width: 150,
    },
    {
      field: 'mobile',
      headerName: 'Mobile',
      width: 130,
    },
    {
      field: 'status',
      headerName: 'Status',
      width: 100,
      renderCell: (params) => (
        <Chip
          label={params.value}
          color={params.value === 'Active' ? 'success' : 'default'}
          size="small"
          sx={{ fontWeight: 'bold' }}
        />
      ),
    },
    {
      field: 'actions',
      type: 'actions',
      headerName: 'Actions',
      width: 120,
      getActions: (params: GridRowParams) => [
        <GridActionsCellItem
          icon={
            <Tooltip title="View User">
              <Visibility />
            </Tooltip>
          }
          label="View"
          onClick={() => handleViewUser(params.row)}
          key="view"
        />,
        <GridActionsCellItem
          icon={
            <Tooltip title="Edit User">
              <Edit />
            </Tooltip>
          }
          label="Edit"
          onClick={() => handleEditUser(params.row)}
          key="edit"
        />,
      ],
    },
  ];

  return (
    <MainLayout>
      <Box>
        {/* Page Header */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="h4" component="h1" sx={{ fontWeight: 'bold', color: 'primary.main', mb: 1 }}>
            User Directory
          </Typography>
          <Typography variant="body1" color="text.secondary">
            Manage user accounts and employee information
          </Typography>
        </Box>

        {/* Main Content Paper */}
        <Paper sx={{ height: 'calc(100vh - 200px)', display: 'flex', flexDirection: 'column' }}>
          
          {/* Toolbar */}
          <Toolbar sx={{ borderBottom: '1px solid', borderColor: 'divider', gap: 2, flexWrap: 'wrap' }}>
            
            {/* Search Bar */}
            <TextField
              placeholder="Search by Name or Employee ID..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              size="small"
              sx={{ minWidth: 300 }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Search />
                  </InputAdornment>
                ),
              }}
            />

            {/* Filters */}
            <FormControl size="small" sx={{ minWidth: 120 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={statusFilter}
                label="Status"
                onChange={(e) => setStatusFilter(e.target.value)}
              >
                <MenuItem value="All">All</MenuItem>
                <MenuItem value="Active">Active</MenuItem>
                <MenuItem value="Inactive">Inactive</MenuItem>
              </Select>
            </FormControl>

            <FormControl size="small" sx={{ minWidth: 160 }}>
              <InputLabel>Designation</InputLabel>
              <Select
                value={designationFilter}
                label="Designation"
                onChange={(e) => setDesignationFilter(e.target.value)}
              >
                <MenuItem value="All">All</MenuItem>
                {getUniqueValues('designation').map((designation) => (
                  <MenuItem key={designation} value={designation}>
                    {designation}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            {/* Spacer */}
            <Box sx={{ flexGrow: 1 }} />

            {/* Action Buttons */}
            <Button
              variant="outlined"
              startIcon={<Upload />}
              onClick={handleBulkUpload}
              sx={{ textTransform: 'none' }}
            >
              Import from Excel
            </Button>

            <Button
              variant="contained"
              startIcon={<Add />}
              onClick={handleCreateUser}
              sx={{ textTransform: 'none' }}
            >
              Create New User
            </Button>

          </Toolbar>

          {/* Data Grid */}
          <Box sx={{ flexGrow: 1 }}>
            <DataGrid
              rows={filteredUsers}
              columns={columns}
              initialState={{
                pagination: {
                  paginationModel: {
                    pageSize: 10,
                  },
                },
              }}
              pageSizeOptions={[5, 10, 25, 50]}
              checkboxSelection
              disableRowSelectionOnClick
              sx={{
                border: 0,
                '& .MuiDataGrid-cell:focus': {
                  outline: 'none',
                },
                '& .MuiDataGrid-row:hover': {
                  backgroundColor: 'rgba(25, 118, 210, 0.04)',
                },
              }}
            />
          </Box>

          {/* Results Summary */}
          <Box sx={{ p: 2, borderTop: '1px solid', borderColor: 'divider', backgroundColor: 'grey.50' }}>
            <Typography variant="body2" color="text.secondary">
              Showing {filteredUsers.length} of {users.length} users
            </Typography>
          </Box>

        </Paper>

        {/* Success/Error Snackbar */}
        <Snackbar
          open={snackbar.open}
          autoHideDuration={4000}
          onClose={() => setSnackbar({ ...snackbar, open: false })}
        >
          <Alert
            onClose={() => setSnackbar({ ...snackbar, open: false })}
            severity={snackbar.severity}
            variant="filled"
            sx={{ width: '100%' }}
          >
            {snackbar.message}
          </Alert>
        </Snackbar>
      </Box>
    </MainLayout>
  );
}